.toggle{
    display: flex;
    justify-content: center;
    border: 3px solid var(--orange);
    border-radius: 1rem;
    position: relative;
    padding: 2px;
    cursor: pointer;
    >*{
        width: 1rem;
        height: 1rem;
        color: var(--orange);
    }
    .t-button{
        border-radius: 100%;
        background: var(--orange);
        position: absolute;
        left: 0.2rem;
    }
}